import React, { Component } from "react";
import { Switch, Route } from "react-router-dom";
import { Link, Redirect } from "react-router-dom";
import Register from "./Register";
import Login from "./Login";
import App from "./App";
import Forget from "./Forget";
import Reset from "./Reset";
import Chat from "./Chat";
const Main = () => (
  <div>
    <Switch>
      <Route exact path="/Go-For-Chat/" component={Register} />
      {console.log("After Register")}
      <Route exact path="/Go-For-Chat/Login" component={Login} />
      <Route exact path="/Go-For-Chat/Forget" component={Forget} />
      <Route exact path="/Go-For-Chat/Chat" component={Chat} />
      <Route exact path="/Go-For-Chat/Reset" component={Reset} />
    </Switch>
  </div>
);
export default Main;
